<template>
    <div class="page">
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                <span class="title_text">
                    兑换红包
                </span>
            </section>
        </header>

        <div class="form">
            <section class="input_c">
                <input type="text" placeholder="请输入兑换码" v-model="redeemCode">
            </section>
            <section class="input_c">
                <div>
                    <input type="text" placeholder="验证码" v-model="capCode">
                </div>

                <div>
                    <img :src="capUrl" alt="">
                    <div @click="changeCap">
                        <p>看不清</p>
                        <p >换一张</p>
                    </div>
                </div>
            </section>
        </div>
        <div class="determine">兑换</div>
    </div>
</template>

<script>
    export default {
        name: "HuanHB",
        data(){
            return{
                capUrl:"",
                capCode:"",
                redeemCode:"",
            }
        },
        methods:{
            goBack() {
                this.$router.push({
                    path:'/benefit',

                })
            },
            requestCode(){
                this.axios.post("v1/captchas").then(res=>{
                    // console.log(res);
                    //获取到的base64可直接赋给图片的URL
                    this.capUrl = res.code;
                }).catch(err=>{})
            },
            changeCap(){
                this.requestCode();
            },

        },
        created(){
            //网页进入，请求验证码
            this.requestCode();
        },
    }
</script>

